import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from 'axios';
import { API_Key } from "../../common/api/movieApiKey";



export const fetchAsyncMovies = createAsyncThunk("movies/fetchAsyncMovies", async (term) => {

    const response = await axios.get(`http://www.omdbapi.com/?s=${term}&apikey=${API_Key}`)
    return response.data;
});

export const fetchAsyncShows = createAsyncThunk('movies/fetchAsyncShows', async (term) => {

    const response = await axios.get(`http://www.omdbapi.com/?s=${term}&apikey=${API_Key}`)
    return response.data;
})

export const fetchAsyncMovieDetails = createAsyncThunk('movies/fetchAsyncMovieDetails', async (id) => {
    const response = await axios.get(`http://www.omdbapi.com/?i=${id}&Plot=full&apikey=${API_Key}`)
    return response.data;
})

const initialState = {
    movies: {},
    shows: {},
    details: {},
    loading: false
}
const movieSlice = createSlice({
    name: "movies",
    initialState,
    reducers: {
        addMovies: (state, { payload }) => {
            state.movies = payload;
        }
    },
    extraReducers: {
        [fetchAsyncMovies.pending]: (state) => {
            state.loading = true;

        },
        [fetchAsyncMovies.fulfilled]: (state, { payload }) => {
            console.log("fetched successfully");
            return { ...state, movies: payload }
        },
        [fetchAsyncMovies.rejected]: () => {
            console.log("fetch rejected");
        },
        [fetchAsyncShows.fulfilled]: (state, { payload }) => {
            console.log("shows fetched");
            return { ...state, shows: payload }
        },
        [fetchAsyncMovieDetails.fulfilled]: (state, { payload }) => {
            console.log("Details fetched");
            return { ...state, details: payload }
        }

    }

});

export const { addMovies } = movieSlice.actions;
export const getAllMovies = state => state.movies.movies;
export const getAllShows = state => state.movies.shows;
export const getMovieDetails = state => state.movies.details;
export const getLoadingState = state => state.movies.loading;

export default movieSlice.reducer;