import React from 'react';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import Slider from 'react-slick';
import { getAllMovies, getAllShows, getLoadingState } from '../../features/movies/movieSlice';
import MovieCard from "../MovieCard/MovieCard"
import { Settings } from '../../common/setting';
import "../MovieListing/MovieListing.scss"


const MovieListing = () => {



    const movies = useSelector(getAllMovies);
    const shows = useSelector(getAllShows);
    const loader = useSelector(getLoadingState);


    let renderMovies, renderShows = "", renderLoader = "";

    renderMovies = movies.Response === "True" ? (
        movies.Search.map((movie, index) => (
            <MovieCard key={index} data={movie} />
        ))) :
        (
            <div className="movies-error">{movies.Error}</div>
        );

    renderLoader = loader === "true" ? (
        movies.Search.map((movie, index) => (
            <MovieCard key={index} data={movie} />
        ))) :
        (
            <div className="movies-error">Loading...</div>
        );


    renderShows = shows.Response === "True" ? (
        shows.Search.map((show, index) => (
            <MovieCard key={index} data={show} />
        ))) :
        (
            <div className="movies-error">{shows.Error}</div>
        );


    return (
        <div className="movie-wrapper">
            <div className="movie-list">
                <h2>Movies</h2>
                <div className="movie-container_1">
                    <Slider {...Settings}>
                        {renderMovies}
                    </Slider>
                </div>
            </div>
            <div className="movie-list">
                <h2>Shows</h2>
                <div className="movie-container_2">
                    <Slider {...Settings}>
                        {renderShows}
                    </Slider>
                </div>
            </div>
        </div>
    );
};

export default MovieListing;